<template>
	<view>
		<view class="page-container">
			<view class="info-card">
				<view class="title-container">
					<text class="title" v-if="!isEditing">{{grassInfo.name}}</text>
					<u-input class="title-input" v-else v-model="editInfo.name" placeholder="请输入牧草名称" border="surround"
						@change="change"></u-input>
					<view class="tag" @click="isEditing ? showGradeSelect() : ''">
						<text v-if="!isEditing">{{grassInfo.grade}}</text>
						<text v-else class="tag-select">{{editInfo.grade || '选择等级'}}</text>
					</view>
				</view>
				<view class="info-item">
					<text class="label">地区：</text>
					<text class="value" v-if="!isEditing">{{grassInfo.location}}</text>
					<u-input class="value-input" v-else v-model="editInfo.location" placeholder="请输入地区"
						border="bottom" />
				</view>
				<view class="info-item">
					<text class="label">品类：</text>
					<text class="value" v-if="!isEditing">{{grassInfo.type}}</text>
					<u-input class="value-input" v-else v-model="editInfo.type" placeholder="请输入品类" border="bottom" />
				</view>
				<view class="info-item">
					<text class="label">总量：</text>
					<text class="value" v-if="!isEditing">{{grassInfo.weight}}</text>
					<u-input class="value-input" v-else v-model="editInfo.weight" placeholder="请输入总量" border="bottom" />
				</view>
				<view class="info-item">
					<text class="label">灰分：</text>
					<text class="value" v-if="!isEditing">{{grassInfo.ash}}</text>
					<u-input class="value-input" v-else v-model="editInfo.ash" placeholder="请输入灰分" border="bottom" />
				</view>
				<view class="info-item">
					<text class="label">水分：</text>
					<text class="value" v-if="!isEditing">{{grassInfo.water}}</text>
					<u-input class="value-input" v-else v-model="editInfo.water" placeholder="请输入水分" border="bottom" />
				</view>
			</view>

			<view class="section">
				<v-title text="照片"></v-title>
				<view class="image-grid">
					<u-grid :col="3" :border="false">
						<u-grid-item v-for="(item, index) in isEditing ? editInfo.images : images" :key="index">
							<v-image :src="item" width="220" height="160" :border-radius="10"
								@click="isEditing ? chooseImage(index) : previewImage(item, images)"></v-image>
							<view class="delete-btn" v-if="isEditing" @click.stop="deleteImage(index)">
								<u-icon name="close" color="#fff" size="20"></u-icon>
							</view>
						</u-grid-item>
						<u-grid-item v-if="isEditing && editInfo.images.length < 9">
							<view class="add-image" @click="chooseImage(-1)">
								<u-icon name="plus" color="#999" size="40"></u-icon>
							</view>
						</u-grid-item>
					</u-grid>
				</view>
			</view>

			<view class="section">
				<v-title text="视频"></v-title>
				<view class="video-item">
					<video :src="isEditing ? editInfo.videoUrl : videoUrl" controls show-center-play-btn show-play-btn
						initial-time="0" class="video-player" poster="/static/imgCs/1.png"></video>
					<view class="edit-video-btn" v-if="isEditing" @click="chooseVideo">
						<u-icon name="reload" color="#fff" size="24"></u-icon>
						<text class="edit-video-text">更换视频</text>
					</view>
				</view>
			</view>

			<!-- 添加占位区域，防止内容被底部footer遮挡 -->
			<view class="footer-placeholder"></view>

			<view class="footer">
				<button class="btn" :class="isEditing ? 'btn-cancel' : 'btn-edit'"
					@click="isEditing ? cancelEdit() : startEdit()">
					{{isEditing ? '取消' : '修改'}}
				</button>
				<!-- <button class="btn" :class="isEditing ? 'btn-save' : 'btn-add'"
					@click="isEditing ? saveEdit() : addNew()">
					{{isEditing ? '保存' : '新增'}}
				</button> -->
			</view>

			<!-- 添加等级选择器 -->
			<u-select v-model="showGradeSelector" :list="gradeOptions" @confirm="confirmGrade"></u-select>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isEditing: false,
				videoUrl: "https://p.ampmake.com/lilibrary/hd/705176540839690/4a1e8cda-d47b-41d9-8fc3-8955cbddffb9.mp4",
				// 使用网络图片，解决预览问题
				images: [
					'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimage109.360doc.com%2FDownloadImg%2F2025%2F04%2F0321%2F296122601_4_20250403090445718&refer=http%3A%2F%2Fimage109.360doc.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1754552945&t=41d5b749ca49c9192962ce7b8547f05d',
					'https://img3.myhsw.cn/2018-06-29/d8qykxd2.jpg?123',
					'https://img1.baidu.com/it/u=467711733,1187695230&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=889',
					'https://club2.autoimg.cn/album/g27/M07/6B/F9/userphotos/2020/06/01/09/500_ChsEnV7UVkSAfus3AAI5OAAhqsg341.jpg',
					'https://image.cnhnb.com/image/jpeg/details/2023/12/26/747fe3aedf1d4caabd5f65438a82450e.jpeg?x-tos-process=image/resize,m_lfit,w_1200/format,jpg,image/auto-orient,1,image/watermark,image_d20ucG5nP3gtdG9zLXByb2Nlc3M9aW1hZ2UvcmVzaXplLFBfMzA=,g_se,x_10,y_10',
					'https://img0.baidu.com/it/u=1349845785,2247634035&fm=253&fmt=auto&app=138&f=JPEG?w=400&h=600'
				],
				grassInfo: {
					name: '牧草相关名字',
					grade: '优级',
					location: '内蒙古呼和浩特特殊军区某街道809',
					type: '燕麦干草',
					weight: '6吨',
					ash: '5%-8%',
					water: '自由水'
				},
				editInfo: {
					name: '',
					grade: '',
					location: '',
					type: '',
					weight: '',
					ash: '',
					water: '',
					images: [],
					videoUrl: ''
				},
				// 新增等级选择器相关数据
				showGradeSelector: false,
				gradeOptions: [{
						value: '1',
						label: '优级'
					},
					{
						value: '2',
						label: '一级'
					},
					{
						value: '3',
						label: '二级'
					}
				]
			};
		},
		onLoad(options) {
			console.log(options);
			// 这里可以根据options获取详细信息
			// this.getGrassDetails(options.id);
		},
		methods: {
			previewImage(current, urls) {
				// 创建完整的URL数组（添加域名前缀，如果是网络图片）
				let imageUrls = [];

				// 处理相对路径图片，确保在小程序中能正确显示
				if (urls && urls.length) {
					urls.forEach(item => {
						// 判断是否为网络图片
						if (item.startsWith('http')) {
							imageUrls.push(item);
						} else {
							// 对于本地图片，需要使用绝对路径
							const imgPath = item;
							imageUrls.push(imgPath);
						}
					});
				} else {
					// 如果只有一张图片
					imageUrls = [current];
				}

				console.log('预览图片', current, imageUrls);

				// 调用预览API
				uni.previewImage({
					current: current,
					urls: imageUrls,
					success: () => {
						console.log('预览图片成功');
					},
					fail: (err) => {
						console.error('预览图片失败', err);
						uni.showToast({
							title: '图片预览失败',
							icon: 'none'
						});
					}
				});
			},
			// 添加 change 方法处理输入变化
			change(value) {
				console.log('输入值变化：', value);
			},
			startEdit() {
				// 进入编辑模式
				this.isEditing = true;
				// 复制当前数据到编辑对象
				this.editInfo = {
					name: this.grassInfo.name,
					grade: this.grassInfo.grade,
					location: this.grassInfo.location,
					type: this.grassInfo.type,
					weight: this.grassInfo.weight,
					ash: this.grassInfo.ash,
					water: this.grassInfo.water,
					images: [...this.images],
					videoUrl: this.videoUrl
				};
			},
			cancelEdit() {
				// 退出编辑模式
				this.isEditing = false;
				uni.showToast({
					title: '已取消编辑',
					icon: 'none'
				});
			},
			saveEdit() {
				// 保存编辑的数据
				this.grassInfo = {
					name: this.editInfo.name,
					grade: this.editInfo.grade,
					location: this.editInfo.location,
					type: this.editInfo.type,
					weight: this.editInfo.weight,
					ash: this.editInfo.ash,
					water: this.editInfo.water
				};

				this.images = [...this.editInfo.images];
				this.videoUrl = this.editInfo.videoUrl;

				// 退出编辑模式
				this.isEditing = false;

				uni.showToast({
					title: '保存成功',
					icon: 'success'
				});

				// 实际应用中，这里应该调用API保存数据
				// this.updateGrassDetails();
			},
			addNew() {
				uni.showToast({
					title: '新增数据',
					icon: 'none'
				});
			},
			chooseImage(index) {
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album', 'camera'],
					success: (res) => {
						const tempFilePaths = res.tempFilePaths;

						if (index === -1) {
							// 添加新图片
							this.editInfo.images.push(tempFilePaths[0]);
						} else {
							// 替换已有图片
							this.$set(this.editInfo.images, index, tempFilePaths[0]);
						}

						// 实际应用中，这里应该上传图片到服务器
						// this.uploadImage(tempFilePaths[0]);
					}
				});
			},
			deleteImage(index) {
				uni.showModal({
					title: '提示',
					content: '确定要删除这张图片吗？',
					success: (res) => {
						if (res.confirm) {
							this.editInfo.images.splice(index, 1);
						}
					}
				});
			},
			chooseVideo() {
				uni.chooseVideo({
					sourceType: ['album', 'camera'],
					maxDuration: 60,
					camera: 'back',
					success: (res) => {
						this.editInfo.videoUrl = res.tempFilePath;

						// 实际应用中，这里应该上传视频到服务器
						// this.uploadVideo(res.tempFilePath);
					}
				});
			},
			getGrassDetails(id) {
				// 从API获取详情数据
				// 示例
				/*
				this.$api.getGrassDetails(id).then(res => {
					if (res.code === 200) {
						this.grassInfo = res.data;
						this.images = res.data.images || [];
						this.videoUrl = res.data.videoUrl || '';
					}
				});
				*/
			},
			updateGrassDetails() {
				// 调用API更新详情数据
				// 示例
				/*
				const params = {
					id: this.grassInfo.id,
					...this.grassInfo,
					images: this.images,
					videoUrl: this.videoUrl
				};
				
				this.$api.updateGrassDetails(params).then(res => {
					if (res.code === 200) {
						uni.showToast({
							title: '保存成功',
							icon: 'success'
						});
					}
				});
				*/
			},

			// 显示等级选择器
			showGradeSelect() {
				if (this.isEditing) {
					this.showGradeSelector = true;
				}
			},

			// 确认选择等级
			confirmGrade(e) {
				this.editInfo.grade = e[0].label;
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}

	::v-deep .u-grid-item-box {
		padding: 0 !important;

		image {
			border-radius: 15rpx;
		}
	}

	.page-container {
		padding: 20rpx 30rpx;
	}

	.title-container {
		display: flex;
		align-items: center;
		margin-bottom: 30rpx;
		justify-content: space-between;

		.title {
			font-size: 34rpx;
			font-weight: bold;
			margin-right: 20rpx;
		}

		.title-input {
			font-size: 34rpx;
			font-weight: bold;
			margin-right: 20rpx;
			flex: 1;
			padding: 4rpx 0;
		}

		::v-deep .u-input__input--surround {
			font-size: 34rpx;
			font-weight: bold;
			border-radius: 10rpx !important;
		}

		::v-deep .u-border-surround:after {
			border-radius: 10rpx !important;
			border-color: #54975d !important;
		}

		.tag {
			width: 130rpx;
			height: 52rpx;
			border-radius: 200rpx 0rpx 0rpx 200rpx;
			background: #54975d;
			opacity: 1;
			color: #f5cb72;
			font-size: 26rpx;
			font-weight: 500;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 8rpx 0rpx;
		}

		.tag-input {
			width: 100%;
			height: 100%;
			text-align: center;
			background: transparent;
			color: #f5cb72;
			font-size: 26rpx;
		}

		::v-deep .tag .u-input__input {
			color: #f5cb72 !important;
			font-size: 26rpx;
			text-align: center;
		}

		::v-deep .tag .u-input {
			padding: 0;
		}
	}

	.info-card {
		background-color: #fff;
		border-radius: 24rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;

		.info-item {
			display: flex;
			margin-bottom: 20rpx;

			&:last-child {
				margin-bottom: 0;
			}

			.label {
				color: #666;
				font-size: 28rpx;
				min-width: 120rpx;
			}

			.value {
				color: #333;
				font-size: 28rpx;
				flex: 1;
			}

			.value-input {
				color: #333;
				font-size: 28rpx;
				flex: 1;
				padding: 4rpx 0;
			}

			::v-deep .u-input {
				flex: 1;
			}

			::v-deep .u-input__input {
				color: #333;
				font-size: 28rpx;
			}

			::v-deep .u-border-bottom:after {
				border-bottom-width: 2rpx !important;
				border-color: #54975d !important;
			}
		}
	}

	.section {
		background-color: #fff;
		border-radius: 24rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;
	}

	.image-grid {
		margin-top: 20rpx;
		position: relative;

		::v-deep .v-image {
			padding: 10rpx;
		}

		.delete-btn {
			position: absolute;
			top: 0;
			right: 0;
			width: 40rpx;
			height: 40rpx;
			background-color: rgba(0, 0, 0, 0.5);
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 10;
		}

		.add-image {
			width: 220rpx;
			height: 160rpx;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #f5f5f5;
			border-radius: 10rpx;
			margin: 10rpx;
			border: 2rpx dashed #ddd;
		}
	}

	.video-item {
		margin-top: 20rpx;
		position: relative;

		.video-player {
			width: 100%;
			height: 400rpx;
			border-radius: 24rpx;
		}

		.edit-video-btn {
			position: absolute;
			right: 20rpx;
			bottom: 20rpx;
			background-color: rgba(0, 0, 0, 0.5);
			color: #fff;
			display: flex;
			align-items: center;
			padding: 10rpx 20rpx;
			border-radius: 30rpx;

			.edit-video-text {
				font-size: 24rpx;
				margin-left: 10rpx;
			}
		}
	}

	.footer {
		display: flex;
		justify-content: space-between;
		padding: 30rpx 30rpx 40rpx 30rpx;
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 999;
		box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);

		.btn {
			width: 100%;
			height: 80rpx;
			line-height: 80rpx;
			text-align: center;
			border-radius: 40rpx;
			font-size: 30rpx;
		}

		.btn-edit {
			background-color: #f1f1f1;
			color: #333;
		}

		.btn-add {
			background-color: #4CAF50;
			color: #fff;
		}

		.btn-cancel {
			background-color: #f1f1f1;
			color: #333;
		}

		.btn-save {
			background-color: #4CAF50;
			color: #fff;
		}
	}

	.footer-placeholder {
		height: 140rpx;
		/* 底部footer的高度+padding */
	}

	.tag-select {
		color: #f5cb72;
		font-size: 26rpx;
		font-weight: 500;
	}
</style>